:root {
  --bs-primary: #3d9ad3;
}

* {
  font-family: 'Ubuntu', sans-serif;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

main {
  letter-spacing: 0.5px !important;
}

body, figure {
  margin: 0 !important;
}

/* Menu Styles */

nav {
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
  position: fixed;
  /* display: flex; */
  display: block;
  top: 0;
  background: white;
  box-shadow: -1px 0px 4px 1px #e4e1e1;
  z-index: 3;
}

section, footer {
  margin: 3rem 1rem;
  word-break: break-word;
  color: #444444;
  text-align: center;
  scroll-margin-top: 6rem;
}


.logo {
  padding-left: 1.5rem;
  margin-top: 11px;
  width: 95px;
}

ul {
  list-style: none;
  padding: 0;
}

ul.nav-links {
  list-style: none;
  padding: 0 1rem;
  margin: 0;
  display: block;
  align-items: center;
  letter-spacing: 1px;
}

a {
  text-decoration: none;
  color: var(--bs-primary);
}

.nav-links > li {
  padding: 1rem;
  padding-left: 0.5rem;
}

.nav-container {
  height: 0;
  transition: height 0.4s ease-out;
  overflow: hidden;
}

li a {
  display: block;
  text-decoration: none;
  color: #444444;
  font-size: 1rem;
}

li a:hover, li a:focus, li a:active, .active {
  color: var(--bs-primary);
}

.menu {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  padding: 0;
  margin-right: 1rem;
  width: 48px;
  float: right;
  outline: none;
}
.line-alt{
  stroke: #0e273a !important;
}
.line {
  fill: none;
  stroke: #000;
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
    stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 6;
}
.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}
.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}
.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
}
.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
}

.h-100 {
  height: 100vh !important;
}

/* Hero */
#hero {
  background: url('./../img/hero/hero.jpg');
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}

#hero:before{
  content: "";
  background: rgba(74, 76, 76, 0.8);
  position: absolute;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}

.hero-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-direction: column;
  color: #fff;
  position: relative;
  text-align: center;
  h1 {
    font-size: 2.5rem;
    padding: 1rem;
  }
}

.get-started {
  font-size: 1.5rem;
  border-radius: 10px;
  margin-top: 2px;
}

.footer {
  margin: 0;
  padding-top: 0;
}

.mas-grid {
  display: grid;
  grid-template-columns: auto;
}

.card {
  margin: 1rem 0;
}


// Utility
.flex-row {
  display: flex;
  flex-direction: row;
}

.justify-between {
  justify-content: space-between;
}


// Overrides
.btn-primary {
  color: #fff;
  background-color: #3d9ad3;
  border-color: #3d9ad3;
  &:hover, &:active, &:focus {
    background-color: #2981b8;
    border-color: #2981b8;
  }
}

